import React, { useEffect, useState } from "react";
import { Table, Button } from "react-bootstrap";

import { useDispatch, useSelector } from "react-redux";
import { Navigate } from "react-router-dom";
import { searchBook, deleteBook, setBookId } from "../../reducers/book";

import "../style.css";

function Home() {
  const dispatch = useDispatch();

  const { books, bookDeleted, bookId } = useSelector((state) => state.book);
  const { user } = useSelector((state) => state.login);

  // const books = [];

  useEffect(() => {
    console.log("from home");
    dispatch(searchBook());
  }, [bookDeleted]);
  const [nav, setNav] = useState(false);


  useEffect(() => {
    if(bookId){
        setNav(true);
    }
    else{
        setNav(false);
    }
  }, [bookId]);

  const removeBook = (id) => {
    if (window.confirm("Are you sure you want to delete?")) {
      dispatch(deleteBook(id));
    }
  };
  const editBook = (id) => {
    dispatch(setBookId(id));
  };

  return (
    <>
      <Table className="tableNew" striped bordered hover>
        <thead>
          <tr>
            <th className="tableNewtr">#</th>
            <th>Name</th>
            <th>
              <div className="row">
                <div className="col-sm-9">Author Name</div>
              </div>
            </th>
            <th>
              <div className="row">
                <div className="col-sm-9">Language</div>
              </div>
            </th>
            <th>
              <div className="row">
                <div className="col-sm-9">Published Year</div>
              </div>
            </th>
            <th>
              <div className="row">
                <div className="col-sm-9">Pages</div>
              </div>
            </th>
            {user?.token ?<th className="tableNewtr"><div className="row">
                <div className="col-sm-9">Action</div>
              </div></th>:""}
          </tr>
        </thead>
        <tbody>
          {books?.map(function (result, i) {
            return (
              <tr key={i}>
                <td className="tableNewtr">{i + 1}</td>
                <td>{result.name}</td>
                <td>{result.author_name}</td>
                <td>{result.language}</td>
                <td>{result.published_year}</td>
                <td>{result?.pages}</td>
                {user?.token ?<th className="tableNewBtn"><Button
                    className="addButton btn-small"
                    variant="danger"
                    onClick={() => {
                        removeBook(result.id);
                    }}
                  >
                    Remove
                  </Button>
                  <Button
                      className="addButton btn-small"
                      variant="primary"
                      onClick={() => {
                        editBook(result.id);
                      }}
                    >
                      Edit
                    </Button>
                    </th>:""}
              </tr>
            );
          })}
        </tbody>
      </Table>
      {nav ? <Navigate to="/update_book" replace={true} />  : "" }

    </>
  );
}

export default Home;
