import { createSlice, createAsyncThunk, createAction } from "@reduxjs/toolkit";


const initialState = {
  loading: false,
  error: null,
  user: null,
  token: null,
};

// export const logoutUser = createAction("LOGOUT_USER");


export const userLogin = createAsyncThunk(
  "login/userLoginApi",
  (arg, { getState }) => {
    const { user } = getState().login;
    if (!user && arg.length !== 0) {
      //have to ensure if the user object is not empty
      return fetch("http://127.0.0.1:8000/api-token-auth/", {
          // calling API with url and config variables
          headers: {
            "Content-Type": "application/json",
          },
          method: "POST",
          body: JSON.stringify(arg),
        })
        .then((response) => {
          if (response.status === 200) {
            return response.json(); //get success response for valid user
          } else {
            throw "error"; //throw error for invalid user
          }
        })
        .then((data) => {
          window.localStorage.setItem("token", data.token);
          return data;
        })
        .catch((error) => {
          const errorMSg =
            error ;
          throw errorMSg;
        });
    } else {
      throw "error";
    }
  }
);

const loginSlice = createSlice({
  name: "login",
  initialState,
  reducers: {
    resetError: (state, action) => {
      state.error = action.payload;
    },
    setUserToken: (state, action) => {
      state.user = action.payload;
    },
    logoutUser: (state, action) => {
      state.user=null;
      state.token=null;
    },
  },
  extraReducers: {
    [userLogin.pending]: (state) => {
      state.loading = true;
    },
    [userLogin.fulfilled]: (state, action) => {
      state.user = action.payload;
      state.loading = false;
    },
    [userLogin.rejected]: (state, action) => {
      state.error = action.error.message;
      state.loading = false;
    },
  },
});

export const { resetError, setUserToken, logoutUser } = loginSlice.actions;
export default loginSlice.reducer;
