// import logo from '../../logo.svg';

import { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { Navigate } from "react-router-dom";
import { useSelector } from "react-redux";
import { updateBook, searchBookById } from "../../reducers/book";

import "../style.css";

function UpdateBook() {
  const [name, setName] = useState('');
  const [authorName, setAuthorName] = useState('');
  const [language, setaLanguage] = useState('');
  const [publishedYear, setPublishedYear] = useState('');
  const [pages, setPages] = useState('');

  const dispatch = useDispatch();

  const { book, bookId } = useSelector((state) => state.book);

console.log("addnew book ....",book)

useEffect(() => {
  dispatch(searchBookById(bookId));
}, []);
const [nav, setNav] = useState(false);

useEffect(() => {
  if(!bookId){
    setNav(true);
  }
}, [bookId]);

useEffect(() => {
  if(book){
  setName(book.name);
  setAuthorName(book.author_name);
  setaLanguage(book.language);
  setPublishedYear(book.published_year);
  setPages(book.pages);
  }
}, [book]);
  const handleSubmit = async (e) => {
    dispatch(
      updateBook({id:bookId,data:{
        name: name,
        author_name: authorName,
        language: language,
        published_year: publishedYear,
        pages: pages,
      }})
    );
  };

  return (
    <div className="addNew">

      <h1>Book Details</h1>
      <form onSubmit={handleSubmit}>
        <label>
          <p>Name</p>
          <input type="text" value={name} onChange={(e) => setName(e.target.value)} />
        </label>{" "}
        <label>
          <p>Author Name</p>
          <input type="text" value={authorName} onChange={(e) => setAuthorName(e.target.value)} />
        </label>{" "}
        <label>
          <p>Language</p>
          <input type="text" value={language} onChange={(e) => setaLanguage(e.target.value)} />
        </label>{" "}
        <label>
          <p>Published Year</p>
          <input
            type="number"
            value={publishedYear}
            onChange={(e) => setPublishedYear(e.target.value)}
          />
        </label>{" "}
        <label>
          <p>No of Pages</p>
          <input type="number" value={pages} onChange={(e) => setPages(e.target.value)} />
        </label>
        <div className="addNew hi">
          <button  type="submit">Update</button>
        </div>
      </form>
      {nav ? <Navigate to="/" replace={true} />  : "" }

    </div>
  );
}

export default UpdateBook;
