// import logo from '../../logo.svg';

import { useState } from "react";
import { useDispatch } from "react-redux";
import { userLogin } from "../../reducers/login";
import { Navigate } from "react-router-dom";
import { useSelector } from "react-redux";
import { addNewBook } from "../../reducers/book";

import "../style.css";


function AddNewBook() {
  const [name, setName] = useState();
  const [authorName, setAuthorName] = useState();
  const [language, setaLanguage] = useState();
  const [publishedYear, setPublishedYear] = useState();
  const [pages, setPages] = useState();

  const dispatch = useDispatch();

  const { user } = useSelector((state) => state.login);
console.log("addnew book ....",user)
  const handleSubmit = async (e) => {
    // e.preventDefault();
    dispatch(
        addNewBook({
        name: name,
        author_name: authorName,
        language: language,
        published_year: publishedYear,
        pages: pages,
      })
    );
  };

  return (
    <div className="addNew">
      {/* {user?.token ? "" :<Navigate to="/" replace={true} />} */}

      <h1>Book Details</h1>
      <form onSubmit={handleSubmit}>
        <label>
          <p>Name</p>
          <input type="text" onChange={(e) => setName(e.target.value)} />
        </label>{"  "}
        <label>
          <p>Author Name</p>
          <input type="text" onChange={(e) => setAuthorName(e.target.value)} />
        </label>{"  "}
        <label>
          <p>Language</p>
          <input type="text" onChange={(e) => setaLanguage(e.target.value)} />
        </label>{"  "}
        <label>
          <p>Published Year</p>
          <input
            type="number"
            onChange={(e) => setPublishedYear(e.target.value)}
          />
        </label>{"   "}
        <label>
          <p>No of Pages</p>
          <input type="number" onChange={(e) => setPages(e.target.value)} />
        </label>
        <div className="addNew hi">
          <button type="submit">Add</button>
        </div>
      </form>
    </div>
  );
}

export default AddNewBook;
