import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";




const initialState = {
  books: [],
  bookId:null,
  book: null,
  bookLoading: null,
  bookError: null,
  bookAdded: null,
  bookAddError: null,
  bookDeleted: false,
  deleteBookError: null,
  bookUpdated: false,

};

export const searchBook = createAsyncThunk(
  "book/searchBook",
  (arg, { getState }) => {
    // const { user } = getState()?.login;
    // const { portSearchSort } = getState()?.port;
    // const token = user.token;
      return fetch(
          `http://127.0.0.1:8000/api/lib/books`,
          {
            // calling API with url and config variables
            headers: {
              "Content-Type": "application/json",
              // Authorization: "Token " + token,
            },
            method: "GET",
          }
        )
        .then((response) => {
          if (response.status === 200) {
            return response.json(); //get success response for valid token
          } else {
            return response.json().then(() => {
              throw "error"; //throw error for invalid user
            });
          }
        })
        .catch((err) => {
          const errorMsg =
            err ;
          throw errorMsg;
        });
  }
);

export const searchBookById = createAsyncThunk(
  "book/searchBookById",
  (arg, { getState }) => {
    const { user } = getState()?.login;
    const token = user.token;
    if(user){
      return fetch(
          `http://127.0.0.1:8000/api/lib/book/${arg}`,
          {
            // calling API with url and config variables
            headers: {
              "Content-Type": "application/json",
              Authorization: "Token " + token,
            },
            method: "GET",
          }
        )
        .then((response) => {
          if (response.status === 200) {
            return response.json(); //get success response for valid token
          } else {
            return response.json().then(() => {
              throw "error"; //throw error for invalid user
            });
          }
        })
        .catch((err) => {
          const errorMsg =
            err ;
          throw errorMsg;
        });
  }}
);

export const addNewBook = createAsyncThunk(
  "book/addNewBook",
  (arg, { getState }) => {
    const { user } = getState()?.login;
    const token = user.token;
    if(user){
      return fetch(
          `http://127.0.0.1:8000/api/lib/add_book`,
          {
            // calling API with url and config variables
            headers: {
              "Content-Type": "application/json",
              Authorization: "Token " + token,
            },
            method: "POST",
            body: JSON.stringify(arg),
          }
        )
        .then((response) => {
          if (response.status === 200) {
            return response.json(); //get success response for valid token
          } else {
            return response.json().then(() => {
              throw "error"; //throw error for invalid user
            });
          }
        })
        .catch((err) => {
          const errorMsg =
            err ;
          throw errorMsg;
        });
  }
}
);

export const updateBook = createAsyncThunk(
  "book/updateBook",
  (arg, { getState }) => {
    const { user } = getState()?.login;
    const token = user.token;
    if(user){
      return fetch(
          `http://127.0.0.1:8000/api/lib/update_book/${arg.id}`,
          {
            // calling API with url and config variables
            headers: {
              "Content-Type": "application/json",
              Authorization: "Token " + token,
            },
            method: "PUT",
            body: JSON.stringify(arg.data),
          }
        )
        .then((response) => {
          if (response.status === 200) {
            return response.json(); //get success response for valid token
          } else {
            return response.json().then(() => {
              throw "error"; //throw error for invalid user
            });
          }
        })
        .catch((err) => {
          const errorMsg =
            err ;
          throw errorMsg;
        });
  }
}
);

export const deleteBook = createAsyncThunk(
  "book/deleteBook",
  (arg, { getState }) => {
    const { user } = getState()?.login;
    const token = user.token;
    if(user){
      return fetch(
          `http://127.0.0.1:8000/api/lib/delete_book/${arg}`,
          {
            // calling API with url and config variables
            headers: {
              "Content-Type": "application/json",
              Authorization: "Token " + token,
            },
            method: "DELETE",
          }
        )
        .then((response) => {
          if (response.status === 200) {
            console.log("response.status === 200")
            return {"deleted":"true"}; //get success response for valid token
          } else {
            return response.json().then(() => {
              throw "error"; //throw error for invalid user
            });
          }
        })
        .catch((err) => {
          const errorMsg =
            err ;
          throw errorMsg;
        });
  }
}
);


const bookSlice = createSlice({
  name: "book",
  initialState,
  reducers: {
    setBookId: (state, action) => {
        state.bookId= action.payload;
    },
  },
  extraReducers: {
    [searchBook.pending]: (state) => {
      state.bookLoading = true;
    },
    [searchBook.fulfilled]: (state, action) => {
      console.log("search book fulfilled")
        state.books = action.payload;
    },
    [searchBook.rejected]: (state, action) => {
      state.bookError = action.payload;
    },

    [searchBookById.pending]: (state) => {
      state.bookLoading = true;
    },
    [searchBookById.fulfilled]: (state, action) => {
        state.book = action.payload[0];
        console.log("state.book------------>",state.book)
    },
    [searchBookById.rejected]: (state, action) => {
      state.bookError = action.payload;
    },

    [addNewBook.pending]: (state) => {
      state.bookLoading = true;
    },
    [addNewBook.fulfilled]: (state, action) => {
      console.log("Add book fulfilled")
        state.bookAdded = action.payload;
    },
    [addNewBook.rejected]: (state, action) => {
      state.bookAddError = action.payload;
    },

    [updateBook.pending]: (state) => {
      state.bookLoading = true;
    },
    [updateBook.fulfilled]: (state, action) => {
      console.log("Add book fulfilled")
        state.bookUpdated = !state.bookUpdated;
    },
    [updateBook.rejected]: (state, action) => {
      state.bookAddError = action.payload;
    },

    [deleteBook.pending]: (state) => {
      state.bookLoading = true;
      console.log("deleteBook.pending")
    },
    [deleteBook.fulfilled]: (state, action) => {
      console.log("state.bookDeleted before",state.bookDeleted);
        state.bookDeleted = !state.bookDeleted;
      console.log("state.bookDeleted after",state.bookDeleted);

    },
    [deleteBook.rejected]: (state, action) => {
      console.log("deleteBook.rejected",action.payload)

      state.deleteBookError = action.payload;
    },
  },
});

export const {
  setBookId,
} = bookSlice.actions;
export default bookSlice.reducer;
