// import logo from '../../logo.svg';

import { useState } from 'react';
import logo from '../../logo-dark.svg';
import { useDispatch,} from "react-redux";
import { userLogin } from '../../reducers/login';
import  { Navigate } from 'react-router-dom'
import { useSelector } from "react-redux";


import "../style.css";


function About() {
    const [username, setUserName] = useState();
    const [password, setPassword] = useState();
    const dispatch = useDispatch();

  const { user } = useSelector((state) => state.login);


  
    const handleSubmit = async e => {
      e.preventDefault();
      console.log("e------->",username,password)
      // {"username": ,"password": }
      dispatch(userLogin({"username": username,"password": password }));
      // return(<Navigate to="/" />)

      
    }
  
    return(
      <div className="logInNew">
       {user?.token ? <Navigate to="/" replace={true} /> :""}

        <h1>Please Log In</h1>
        
        <form className="logInNew" onSubmit={handleSubmit}>
          <label>
            <p>Username</p>
            <input type="text" onChange={e => setUserName(e.target.value)} />
          </label>
          <br></br>
          <label>
            <p>Password</p>
            <input type="password" onChange={e => setPassword(e.target.value)} />
          </label>
          <br></br>

          <div>
            <button type="submit">Submit</button>
          </div>
        </form>
      </div>
    )
}

export default About;