import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';

import logo from '../../logo-dark.svg';
import { useSelector, useDispatch } from "react-redux";
import { logoutUser } from '../../reducers/login';
import { Navigate } from 'react-router-dom';
import { useEffect, useState } from 'react';


function Header() {
  const { user } = useSelector((state) => state.login);
  const dispatch = useDispatch();
  const [nav, setNav] = useState(false);

  const logout = () => {
    dispatch(logoutUser());
    window.localStorage.clear();
    // setNav(true);
  };
  useEffect(() => {
  }, [user]);
  return (
    <>
      {nav ? <Navigate to="/" replace={true} />  : "" }
      <Navbar bg="dark" variant="dark">
        <Container>
          <Navbar.Brand href="/">
            <img
              alt="logo"
              src={logo}
              width="35"
              height="35"
              className="d-inline-block align-top"
            />{' '}
            Library
          </Navbar.Brand>
          <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="me-auto">
            
          </Nav>
          <Nav>
            {user?.token ? <Nav.Link href="/">Home</Nav.Link>:""}
            {user?.token ? <Nav.Link href="/add_book">Add New Book</Nav.Link>:""}
            {user?.token ?"":<Nav.Link eventKey={2} href="/login">
              Login
            </Nav.Link>}
            {user?.token ?<Nav.Link eventKey={2} onClick={() => {
                        logout();
                      }}>
              Logout
            </Nav.Link>:""}
          </Nav>
        </Navbar.Collapse>

        </Container>
      </Navbar>
    </>
  );
}

export default Header;